@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,700;1,500&display=swap);
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
.header {
    display: flex;
    justify-content: space-around;
}

.nav-list {
    display: flex;
    justify-content: space-around;
    margin: 10px 0;
    align-items: center;
    min-width: 90%;
    z-index: 999;
}
.nav-item {
    list-style: none;
}

.bars-cont {
    display: flex;
    width: 30px;
    height: 30px;
    display: none;
    margin-top: 30px;
}
 .bars {
     color: #fff;
     margin-left: 10px;
     cursor: pointer;
 }
.nav-logo:first-child  img{
    width: 63px;
    margin-top: 20px;
    height: 52px;
}

.nav-list .bandera {
    width: 42px;
    height: 42px;
}

#img-idioma {
    width: 42px;
    height: 42px;
    
}
.nav-item a {
    text-decoration: none;
    font-size: 26px;
    color: #CD5A69;
}
.nav-item a:hover {
    color: #fff;
    border-bottom: 2px solid #CD5A69;
}
.active {
    color: #fff!important;
    border-bottom: 2px solid #CD5A69;
    font-weight: medium;
}

@media screen and (max-width : 1024px) {
    .nav-item a {
        font-size: 18px;
    }
    #img-idioma {
        margin-top: 5px;
        width: 32px;
        height: 32px;
        
    }
    .nav-item:first-child  img{
        margin-top: 10px;
        width: 53px;
        height: 42px;
    }
}

@media  screen and (max-width: 768px) {
        .header {
            position: relative;
            justify-content:space-between;
        }
        
       .nav-list {
            display: flex;
            flex-direction: column;
            background-color: #092c42;
            height:600px;
            position: absolute;
            top: 80px;
            width: 100%;
            transition: all .5s ease;
            opacity: 1;

       } 
       .bars-cont {
           display: block;
           margin: 50px 20px 0 0;
       }
       .logo {
            margin: 30px 20px;
       }
       .nav-item a {
           font-size: 26px;
           
       }
}

